.catalog {
  padding-top: 95px;
  padding-bottom: 120px;
  padding-left: 40px;
  padding-right: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.catalog__title {
  margin: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 1.5;
  margin-bottom: 60px;
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  width: 100%;
  justify-content: center;
}

.catalog__item {
  flex: 0 0 260px;
}
